<template>
    <div class="event">
        <div data-aos="slide-left"
             data-aos-offset="200"
             data-aos-duration="800"
             data-aos-easing="ease-in-out" class="event-holder">
            <img class="event-holder-banner" :src="event[activeIndex].poster" alt="">
            <div class="event-holder-data">
                <div class="arrow">
                    <img v-on:click="leftEventToggle" class="arrow-left" src="../assets/arrow.png" alt="">
                    <img v-on:click="rightEventToggle" class="arrow-right" src="../assets/arrow.png" alt="">
                </div>
                <div class="title">
                    <h1>{{event[activeIndex].name}}</h1>
                    <span>{{event[activeIndex].tagline}}</span>
                </div>
                <div class="event-holder-data-details">
                    <p>{{event[activeIndex].date}}</p>
                    <div class="event-holder-data-details--img">
                        <img src="../assets/rec.png" alt="">
                        <img src="../assets/rec.png" alt="">
                        <img src="../assets/rec.png" alt="">
                    </div>
                    <span>Rs.{{event[activeIndex].fee}}</span>
                </div>
            </div>

        </div>

        <div data-aos="fade-up"
             data-aos-offset="200"
             data-aos-duration="800"
             data-aos-easing="ease-in-out" class="event-down">
            <div class="event-down-left">
                <!--                <h3>Sapphire 1.0</h3>-->
                <div class="gallery">
                    <img @click="activeIndex = 1" class="left-2" :src="event[activeIndex].image"
                         alt="">
                    <img @click="activeIndex = 2" class="left-1" :src="event[activeIndex].image"
                         alt="">
                    <img @click="activeIndex = 0" class="main" :src="event[activeIndex].image"
                         alt="">
                    <img @click="activeIndex = 3" class="right-2" :src="event[activeIndex].image"
                         alt="">
                    <img @click="activeIndex = 4" class="right-1" :src="event[activeIndex].image"
                         alt="">
                </div>
            </div>
            <div class="event-down-right">
                <h3>{{event[activeIndex].name}}</h3>
                <p class="content">{{event[activeIndex].discription}}</p>
                <div class="more">
                    <div data-aos="fade-up"
                         data-aos-offset="200"
                         data-aos-duration="800"
                         data-aos-delay="500"
                         data-aos-easing="ease-in-out" class="more-detials">
                        <p>Date: <span>{{event[activeIndex].date}}</span></p>
                        <p>Time: <span>{{event[activeIndex].time}}</span></p>
                        <p>Venue: <span>{{event[activeIndex].venue}}</span></p>
                    </div>
                    <div class="reg">
                        <router-link tag="a" to="/regSelection">Register Now</router-link>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "EventComponentNew",
        data() {
            return {
                activeIndex: 0,
                event: Array
            }
        },
        props: {
            eventData: Array,
        },
        methods: {
            leftEventToggle() {
                if (this.activeIndex > 0) {
                    this.activeIndex = this.activeIndex - 1;
                    console.log(this.activeIndex)
                }
            },
            rightEventToggle() {
                if (this.activeIndex < this.eventData.length - 1) {
                    this.activeIndex = this.activeIndex + 1;
                    console.log(this.activeIndex)
                }

            }
        },
        created() {
            this.event = this.eventData
            if (!this.event) {
                axios.get('/api/events').then(events => {
                    this.event = events.data.toArray();
                }).catch(err => {
                    console.log(err)
                })
            }
        }
    }
</script>

<style scoped lang="scss">
    .event {
        width: 92%;
        display: flex;
        flex-direction: column;
        align-items: center;

        &-holder {
            display: flex;
            justify-content: center;
            position: relative;

            &-banner {
                width: 93vw;
            }

            &-data {
                position: absolute;
                width: 89vw;
                height: 100%;
                /*background-color: rgba(red, 0.2);*/
                & .arrow {
                    display: flex;
                    justify-content: space-between;
                    padding-left: 50px;
                    padding-right: 50px;
                    align-items: center;
                    height: 100%;

                    &-left {
                        cursor: pointer;
                    }

                    &-right {
                        cursor: pointer;
                        transform: rotate(180deg);
                    }
                }

                & .title {
                    position: absolute;
                    top: 40%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    color: white;
                    font-family: Poppins, sans-serif;
                    font-size: 3vw;
                    font-weight: bolder;

                    & span {
                        font-size: 2vw;
                        color: #EF5939;
                        font-weight: bolder;
                        transform: translate(100px, 0);
                    }
                }


                &-details {
                    display: flex;
                    width: 90%;
                    justify-content: space-between;
                    position: absolute;
                    bottom: 15%;
                    font-size: 2vw;
                    font-weight: bolder;
                    color: white;
                    font-family: Poppins, sans-serif;
                    padding-left: 5%;

                    & p {
                        font-weight: bolder;
                    }

                    & span {
                        color: #EF5939;
                        font-weight: bolder;
                    }

                    &--img {
                        display: flex;
                        width: 10%;
                        margin-top: auto;
                        justify-content: space-around;
                        align-items: center;
                        height: min-content;

                        & img {

                        }
                    }
                }

            }
        }


        &-down {
            display: flex;
            width: 90vw;
            margin-top: 50px;
            justify-content: center;

            &-left {
                width: 100%;

                & h3 {
                    color: white;
                    font-family: Poppins, sans-serif;
                    font-size: 2vw;
                }

                & .gallery {
                    display: flex;
                    height: 100%;
                    justify-content: center;

                    & > img {
                        position: absolute;
                        width: 20%;
                        box-shadow: black 0 4px 10px;

                        &:hover {
                            box-shadow: black 0 4px 50px;
                        }
                    }

                    & .left-2 {
                        transform: translateX(-15vw) scale(0.5);
                        z-index: 250;

                        &:hover {
                            z-index: 900;
                            transform: translateX(-15vw) scale(1);
                            border: #EF5939 solid 2px;
                        }
                    }

                    & .left-1 {
                        transform: translateX(-7.5vw) scale(0.8);
                        z-index: 500;

                        &:hover {
                            z-index: 900;
                            transform: translateX(-7.5vw) scale(1);
                            border: #EF5939 solid 2px;
                        }
                    }

                    & .main {
                        z-index: 800;

                        &:hover {
                            z-index: 900;
                            border: #EF5939 solid 2px;
                        }
                    }

                    & .right-1 {
                        transform: translateX(7.5vw) scale(0.8);
                        z-index: 500;

                        &:hover {
                            z-index: 900;
                            transform: translateX(7.5vw) scale(1);
                            border: #EF5939 solid 2px;
                        }
                    }

                    & .right-2 {
                        transform: translateX(15vw) scale(0.5);
                        z-index: 250;

                        &:hover {
                            z-index: 900;
                            transform: translateX(15vw) scale(1);
                            border: #EF5939 solid 2px;
                        }
                    }

                }

            }

            &-right {
                width: 100%;
                color: white;
                font-family: Poppins, sans-serif;

                & h3 {
                    font-size: 2.2vw;
                    font-weight: bolder;
                    margin-bottom: 10px;
                }

                .content {
                    font-size: 16px;
                    font-family: Helvetica, sans-serif;
                    margin-bottom: 10px;
                }

                & span {
                    font-weight: bolder;
                    color: #EF5939;
                    font-size: 1.3vw;
                }

                &:before {
                    content: ' ';
                    color: #EF5939;
                    height: 100%;
                    width: 2px;
                }

                & .reg {
                    & a {
                        background-color: #EF5939;
                        color: #ffffff;
                        width: 25%;
                        text-align: center;
                        padding: 10px;
                        font-size: 1.3vw;
                        margin-left: auto;
                        border-radius: 60px;
                        text-decoration: none;
                        display: block;
                        margin-top: 20px;
                    }
                }
            }

        }
    }

    @media only screen and (max-width: 700px) {
        .arrow {
            padding: 0;

            &-left {
                margin-left: -20px;
                width: 10px;
            }

            &-right {
                margin-right: -20px;
                width: 10px;
            }
        }
        .event-down {
            flex-direction: column;
            align-items: center;

            & .gallery {
                margin-bottom: 150px;
            }
        }
    }

</style>