                <input v-model="formData.player5name" type="text" placeholder="Player 5 Name">
                <input v-model="formData.player5Reg" type="text" placeholder="Player 5 Reg no.">
                <input v-model="formData.player6name" type="text" placeholder="Player 6 Name">
                <input v-model="formData.player6Reg" type="text" placeholder="Player 6 Reg no.">
            </div>
            <div v-if="this.step === 4" class="input">
