                <input v-model="formData.player8name" type="text" placeholder="Player 8 Name">
                <input v-model="formData.player8Reg" type="text" placeholder="Player 8 Reg no.">
                <input v-model="formData.player9name" type="text" placeholder="Player 9 Name">
                <input v-model="formData.player9Reg" type="text" placeholder="Player 9 Reg no.">
            </div>
            <div v-if="this.step === 5" class="input">
                <input v-model="formData.player10name" type="text" placeholder="Player 10 Name">
                <input v-model="formData.player10Reg" type="text" placeholder="Player 10 Reg no.">
                <input v-model="formData.player11name" type="text" placeholder="Player 11 Name">
                <input v-model="formData.player11Reg" type="text" placeholder="Player 11 Reg no.">
                <input v-model="formData.player12name" type="text" placeholder="Player 12 Name">
                <input v-model="formData.player12Reg" type="text" placeholder="Player 12 Reg no.">
            </div>
            <div v-if="this.step === 6" class="input">
                <input v-model="formData.player13name" type="text" placeholder="Player 13 Name">
                <input v-model="formData.player13Reg" type="text" placeholder="Player 13 Reg no.">
                <input v-model="formData.player14name" type="text" placeholder="Player 14 Name">
                <input v-model="formData.player14Reg" type="text" placeholder="Player 14 Reg no.">
                <input v-model="formData.player15name" type="text" placeholder="Player 15 Name">
                <input v-model="formData.player15Reg" type="text" placeholder="Player 15 Reg no.">
            </div>
            <div class="buttons">
                <button v-if="this.step < 1" v-on:click="back">Back</button>
                <button v-if="this.step < 7" style="margin-left: auto" v-on:click="next">Next</button>
            </div>
        </div>
        <div v-if="this.step < 7" class="registration-right">
            <div class="reg-right">
                <p>Witness</p>
                <span class="small">the</span>
                <span class="large">Change</span>
            </div>
            <img class="reg-img" src="../assets/reg.png" alt="">
        </div>

        <div v-if="this.step === 7" class="registration-left">
            <h2 style="font-size: 3vw;color: white">Accommodation Required</h2>
            <button v-on:click="yes" tag="button"
                    style="background-color: #EF5939"
                    class="button" to="/reg">Yes
            </button>
            <button v-on:click="not" tag="button" style="background-color: #2D5D7C"
                    class="button" to="/reg">No
            </button>
        </div>
        <div v-if="this.step === 8 && this.message!==true" class="registration-left">
            <h2 style="font-size: 3vw;color: white">Successful</h2>
            <button v-on:click="register" tag="button"
                    style="background-color: green"
                    class="button">Submit
            </button>
        </div>
        <div v-if="message" class="registration-left">
            <h2 style="font-size: 3vw;color: white">Registration Successful</h2>

        </div>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "RegistrationComponents",
        data() {
            return {
                message: false,
                activeIndex: 0,
                step: 1,
                formData: {
                    teamName: 'Nitish',
                    captain: {
                        name: 'Nitish',
                        reg: 11701703,
                        email: 'nkpc14@gmail.com',
                        contact: 7376977077,
                        whatsApp: 7376977077,
                    },
                    viceCaptain: {
                        name: 'Nitish',
                        reg: 11701703,
                        email: 'nkpc14@gmail.com',
                        contact: 7376977077,
                    },
                    player3name: 'Nitish',
                    player3Reg: 11701703,
                    player4name: 'Nitish',
                    player4Reg: 11701703,
                    player5name: 'Nitish',
                    player5Reg: 11701703,
                    player6name: 'Nitish',
                    player6Reg: 11701703,
                    player7name: 'Nitish',
                    player7Reg: 11701703,
                    player8name: 'Nitish',
                    player8Reg: 11701703,
                    player9name: 'Nitish',
                    player9Reg: 11701703,
                    player10name: 'Nitish',
                    player10Reg: 11701703,
                    player11name: 'Nitish',
                    player11Reg: 11701703,
                    player12name: 'Nitish',
                    player12Reg: 11701703,
                    player13name: 'Nitish',
                    player13Reg: 11701703,
                    player14name: 'Nitish',
                    player14Reg: 11701703,
                    player15name: 'Nitish',
                    player15Reg: 11701703,
                    accomodation: false,
                    externalTeam: false,
                }
            }
        },
        props: {
            eventData: Array,
        },
        methods: {
            back() {
                if (this.step > 1) {
                    this.step = this.step - 1;
                }
            },
            next() {
                if (this.step < 8) {
                    this.step = this.step + 1;
                }
            },
            register() {
                console.log(this.formData)
                let formPostData = new FormData()
                formPostData.append('team_name', this.formData.teamName)
                formPostData.append('captain_name', this.formData.captain.name)
                formPostData.append('captain_registration_number', this.formData.captain.reg)
                formPostData.append('email', this.formData.captain.email)
                formPostData.append('contact_number', this.formData.captain.contact)
                formPostData.append('whatsapp_number', this.formData.captain.whatsApp)
                formPostData.append('vice_captain_name', this.formData.viceCaptain.name)
                formPostData.append('vice_captain_registration_number', this.formData.viceCaptain.reg)
                formPostData.append('vice_email', this.formData.viceCaptain.email)
                formPostData.append('vice_contact_number', this.formData.viceCaptain.contact)
                formPostData.append('player_name3', this.formData.player3name)
                formPostData.append('player_registration_number3', this.formData.player3Reg)
                formPostData.append('player_name4', this.formData.player4name)
                formPostData.append('player_registration_number4', this.formData.player4Reg)
                formPostData.append('player_name5', this.formData.player5name)
                formPostData.append('player_registration_number5', this.formData.player5Reg)
                formPostData.append('player_name6', this.formData.player6name)
                formPostData.append('player_registration_number6', this.formData.player6Reg)
                formPostData.append('player_name7', this.formData.player7name)
                formPostData.append('player_registration_number7', this.formData.player7Reg)
                formPostData.append('player_name8', this.formData.player8name)
                formPostData.append('player_registration_number8', this.formData.player8Reg)
                formPostData.append('player_name9', this.formData.player9name)
                formPostData.append('player_registration_number9', this.formData.player9Reg)
                formPostData.append('player_name10', this.formData.player10name)
                formPostData.append('player_registration_number10', this.formData.player10Reg)
                formPostData.append('player_name11', this.formData.player11name)
                formPostData.append('player_registration_number11', this.formData.player11Reg)
                formPostData.append('player_name12', this.formData.player12name)
                formPostData.append('player_registration_number12', this.formData.player12Reg)
                formPostData.append('player_name13', this.formData.player13name)
                formPostData.append('player_registration_number13', this.formData.player13Reg)
                formPostData.append('player_name14', this.formData.player14name)
                formPostData.append('player_registration_number14', this.formData.player14Reg)
                formPostData.append('player_name15', this.formData.player15name)
                formPostData.append('player_registration_number15', this.formData.player15Reg)
                formPostData.append('accommodation', this.formData.accomodation)
                axios.post('/api/internalReg/', formPostData).then(data => {
                    if (data) {
                        this.message = true
                    }
                }).catch(err => {
                    console.log(err)
                })

            },
            not() {
                this.formData.accomodation = false
                this.step = 8
            },
            yes() {
                this.formData.accomodation = true
                this.step = 8
            }
        },

    }
</script>

