import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home,
            children: [
                {
                    path: '/event',
                    name: 'event',
                    component: () => import('./components/EventComponent.vue')
                }, {
                    path: '/reg',
                    name: 'reg',
                    component: () => import('./components/RegistrationComponents.vue')
                }, {
                    path: '/regSelection',
                    name: 'regSelection',
                    component: () => import('./components/RegistrationSelection.vue')
                }, {
                    path: '/about',
                    name: 'about',
                    component: () => import('./components/AboutComponent.vue')
                }, {
                    path: '/contactUs',
                    name: 'contactUs',
                    component: () => import('./components/ContactUsComponent.vue')
                }, {
                    path: '/regExternal',
                    name: 'regExternal',
                    component: () => import('./components/ExternalRegistrationComponents.vue')
                }
            ]
        },

    ],
    mode: 'history'
})
